.Footer{
    background-color: rgba(2,32,131,.986);
    min-height: 10vh;
    display: flex;
    flex-direction: row;
    align-self: flex-end;
    align-items: center;
    justify-content: space-around;
    color: white;
    position: absolute;
    bottom: 0;
    width: 100%;
    font-size:large;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    

}
p{
    text-align: center;
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    top: 25%;
}